.block {
  display: flex;
  justify-content: center;
  /* height: calc(100vh - 86px); */
  margin-top: 120px;
}
.nav {
  display: flex;
  flex-direction: column;
  max-width: 320px;
  width: 100%;
  margin-right: 60px;
}

.navBar {
  display: flex;
  flex-direction: column;
}

.navLink {
  text-decoration: none;
  color: #8585ad;
}

.activeNavLink {
  color: #f2f2f3;
}

.navText {
  margin-top: 80px;
}

.profileBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 328px;
}

.ordersBlock {
  max-width: 480px;
  height: calc(100vh - 300px);
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #8585ad;
  scrollbar-track-color: #2f2f37;
}

.ordersBlock::-webkit-scrollbar {
  width: 8px;
}

.ordersBlock::-webkit-scrollbar-track {
  background-color: #2f2f37;
}

.ordersBlock::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}
